.profile__avatar-cover{
  opacity: 0;
  
  position: absolute;
  inset: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: rgba(0, 0, 0, .8);
  transition: opacity .15s linear;
}

.profile__avatar-container:hover .profile__avatar-cover {
  opacity: 1;
}